import { Link } from "react-router-dom";

const Button = function ({
  isFixed = false, //  css
  isClickable = true,
  isRev = false, //
  toggleRef = null, //
  defaltRefs = [], // 
  defaltValues = [], // 
  toggleChanged = null, //onClick  
  setToggleChanged = null, //onClick  
  value, //
  divStyle, // css
  href, //
  style, //Link css
  onClick,
  title, //
  btnClass,
  download = false,
}) {
  //     
  var buttonClassName = isRev ? "my_button_rev" : "my_button";
  if (isFixed) {
    buttonClassName += " fixed";
  }
  if (!isClickable) {
    buttonClassName += " notClicked";
  }

  return (
    <div className={buttonClassName} value={value} style={divStyle}>
      <Link
        className="ripple"
        to={href}
        style={style}
        data-value={value}
        download={download}
        onClick={(e) => {
          if (onClick) {
            onClick(e);
          }
          if (toggleRef) {
            toggleRef.current = !toggleRef.current;
            defaltRefs.forEach((defaltRef, i) => {
              defaltRef.current = defaltValues[i];
            });
            setToggleChanged(!toggleChanged);
          }
        }}
      >
        {title}
      </Link>
    </div>
  );
};

export default Button;
