const DarkButton = function ({
  isRev = false, //
  value, //
  title, //
}) {
  //     
  var buttonClassName = isRev ? "myDarkButton rev" : "myDarkButton ";

  return (
    <div
      className={buttonClassName}
      value={value}
      onClick={onclick}
      data-value={value}
    >
      {title}
    </div>
  );
};

export default DarkButton;
