const shortEngrav = function (eng) {
  if (eng == " ") {
    return "";
  } else if (eng == "   ") {
    return "";
  } else {
    return eng;
  }
};

const isGongElixer = function (elixer) {
  const GongElixer = [
    "",
    "",
    " ",
    "",
    "",
    "",
    "",
    "",
    "",
    " ",
    " ",
    " ",
    " ",
    " ",
  ];
  if (GongElixer.includes(elixer)) return true;
  return false;
};

const shortElixer = function (elixer) {
  var ret = elixer;
  if (elixer == " ") {
    ret = "";
  } else if (elixer == " ") {
    ret = "";
  } else if (elixer == "  ") {
    ret = "";
  } else if (elixer == " ") {
    ret = "";
  } else if (elixer == " ") {
    ret = "";
  } else if (elixer == " ") {
    ret = "";
  }
  return ret.replace(" ", "");
};

